@import '~antd/dist/antd.css';

#root {
    display: flex;
    flex: 1;
    height: 100%;
}

.account-data .ant-card-head {
    height: 65px;
}

.account-data h4,
.account-data p {
    overflow: hidden;
    text-overflow: ellipsis;
}

.ant-menu-item.approve-celr {
    position: absolute;
    bottom: 0;
}

.ant-statistic-content {
    overflow: hidden;
    text-overflow: ellipsis;
}

.ant-input-number {
    width: 315px;
}
